.App{
  display: flex;
  flex-direction: column;
  /* width: 100vw;
  height: 100; */
  overflow: hidden;
   
}

.MyHeader{
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 1px 4px 4px black;
  overflow: hidden;
}

.soppingCartIcon{
  font-size: 30px;
  margin-right: 8px;
}

.pageContent{
  display: flex;
  flex: 1;
  overflow-y: auto;
  margin-top: 2px;
}

.MyFooter{
  height: 60px;
  background-color: black;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.itemCard, .itemCardBadge{
  margin: 10px;
}

.itemCardImage{
  height: 150px !important;
  object-fit: scale-down;
}

@media screen and (max-width: 600px) {

  .App{
      display: block;
      overflow: hidden;
  }

  
}